import {createRouter, createWebHashHistory} from 'vue-router'

const routes = [
  {path: '/', redirect: '/login'},
  {path: '/login', name: 'login', component: () => import('../views/LoginView.vue')},
  {
    path: '/home', name: 'home', component: () => import('../views/HomeView.vue'),
    children: [
      {path: '', redirect: '/HomePageView'},
      {path: '/HomePageView', name: 'HomePageView', component: () => import('../views/home/HomePageView')},
      // 系统管理 页面
      {path: '/user', name: 'user', component: () => import('../views/basics/user/userAdminView')},
      {path: '/role', name: 'role', component: () => import('../views/basics/role/RoleView')},
      {path: '/resource', name: 'resource', component: () => import('../views/basics/ResourceInfo/ResourceInfoView')},
      {path: '/dict', name: 'dict', component: () => import('../views/basics/dict/dictView')},
      // 产品管理 页面
      {path: '/series', name: 'series', component: () => import('../views/spuData/series/seriesView')},
      {path: '/function', name: 'function', component: () => import('../views/spuData/function/functionView')},
      {path: '/spu', name: 'spu', component: () => import('../views/spuData/spu/spuView')},
      // 产品工单 页面
      {path: '/afterSpu', name: 'afterSpu', component: () => import('../views/spuOred/afterSpu/afterSpuView')},
      {path: '/afterCareof', name: 'afterCareof', component: () => import('../views/spuOred/afterCareof/afterCareofView')},
      {path: '/spuChart', name: 'spuChart', component: () => import('../views/spuOred/spuChart/spuChartView')},
      // 平台工具
      {path: '/apiword', name: 'apiword', component: () => import('../views/platform/apiword/apiword')},
      // 客诉页面
      {path: '/problem', name: 'problem', component: () => import('../views/clientPage/problem/problemView')},
      {path: '/message', name: 'message', component: () => import('../views/clientPage/message/messagePushView')},
      {path: '/region', name: 'region', component: () => import('../views/clientPage/region/regionView')},
      {path: '/mailbox', name: 'mailbox', component: () => import('../views/clientPage/mailbox/mailboxView')},
    ]
  },
  {path: '/clientele', name: 'clientele', component: () => import('../clientView/after_from/clienteleVeiw')},
  {path: '/selectAfter/:selcetUrl', name: 'selectAfter', component: () => import('../clientView/after-flow/selectVeiw')},
]

// 添加一个捕获所有路由的路由，用于处理错误
routes.push({ path: '/:catchAll(.*)', name: 'error', component: () => import('../views/home/Error') });


const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router

// to：路由对象，即将要进入的目标
// from：表示从哪里来
// next：回调函数，通过 next 回调函数你可以让 vue-router 去处理导航、取消导航、重定向到其它地方或执行其它操作。
// 配置路由守卫
router.beforeEach((to, from, next) => {
  // 是否是去登录
  if (to.path == '/login') {
    // 如果要去登录，直接放行
    next();
  } else if (to.path == '/clientele' || to.path.includes('/selectAfter')) {
    // 客户端页面，直接放行
    next();
  } else {
    let token = window.localStorage.getItem('token');
    // token不存在，去登录
    if (!token) {
      next('/login')
      return;
    }else {    // 否则放行
      next();
    }

    // let expTime = JSON.parse(atob(token.split('\.')[1])).exp;
    // let nowtime = (new Date()).getTime() / 1000;
    // if (nowtime >= expTime) { // 如果过期，去登录
    //     next('/login');
    // } else {    // 否则放行
    //     next();
    // }
  }
});
